<template>
  <div class="hot">
    <template v-if="hotSongItems">
      <div class="header">
        <div class="logoImg"></div>
        <div class="updateTime">更新日期 04月30日</div>
      </div>

      <ul>
        <NewSongList
          v-for="(newSong,index) in arr"
          :key="index"
          :newSongItem="newSong"
          :songInfo="{order:index,isShowInfo:true,newSongItem:newSong}"
          @current-song="$emit('current-song', $event)"
        ></NewSongList>
      </ul>

      <div v-if="showMore" class="more" @click="showAll">
        查看完整榜单
        <van-icon name="arrow" class="arrow" />
      </div>
    </template>
  </div>
</template>

<script>
import NewSongList from "@/components/NewSongList.vue";

export default {
  name: "Hot",
  data() {
    return {
      hotSongItems: [],
      showMore: true,
      arr: []
    };
  },
  components: { NewSongList },
  methods: {
    getHotSongItems() {
      this.axios
        .get("/top/list?idx=1")
        .then(response => {
          this.hotSongItems = response.data.playlist.tracks;
          this.toggleArr(this.showMore);
          window.localStorage.setItem(
            "hotSongItems",
            JSON.stringify({
              expire: Date.now() + 24 * 60 * 60 * 1000,
              result: response.data.playlist.tracks
            })
          );
        })
        .catch(error => {
          // console.log(error);
          error;
        });
    },

    // 获取前20个歌曲
    randomHotSongItems() {
      return [...this.hotSongItems].slice(0, 20);
    },

    toggleArr(m) {
      return (this.arr = m ? this.randomHotSongItems() : this.hotSongItems);
    },
    showAll() {
      this.showMore = false;
      this.toggleArr(this.showMore);
    }
  },
  created() {
    const cacheHotSongItems = JSON.parse(
      window.localStorage.getItem("hotSongItems")
    );
    if (cacheHotSongItems && cacheHotSongItems.expire > Date.now()) {
      // 存在并且还没有过期
      this.hotSongItems = cacheHotSongItems.result;
      this.toggleArr(this.showMore);
    } else {
      // 已经过期
      this.getHotSongItems();
    }
  }
};
</script>

//  <div class="header">
//         <div class="logoImg"></div>
//         <div class="updateTime"></div>
//       </div>

<style lang="less" scoped>
.hot {
  margin-bottom: 40px;
}
.header {
  width: 100%;
  height: 145px;
  background: #aa5f4a;
  padding: 20px 15px;
  .logoImg{
    width: 142px;
    height: 67px;
    background-image: url(../assets/index_icon_2x.png);
    background-position: -24px -30px;
    background-size: 166px 97px;
  }
  .updateTime{
    color:#f5f5f5;
     margin-top:10px;
  }
}
.more {
  height: 40px;
  line-height: 40px;
  background: #f5f5f5;
  font-size: 16px;
  text-align: center;
  color: #999999;
}
.arrow {
  vertical-align: middle;
}
</style>